@import url(https://fonts.googleapis.com/css2?family=Spartan:wght@100;200;300;400;500;600;700;800;900&display=swap);
@import url(https://stackpath.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css);
body {
  margin: 0;
  font-family: 'Spartan', sans-serif !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
* {
  margin: 0;
}

body {
  background-color: #f5f5f5;
}

.app {
  display: flex;
  justify-content: center;
  align-items: center;  
  padding: 20px ;
}

.app__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.app__left {
  flex: 0.5 1;
}

.app__stats {
  display: flex;
  justify-content: space-between;
}

.MuiMenuItem-root, .MuiSelect-outlined.MuiSelect-outlined {
  font-family: 'Spartan', sans-serif !important;
}
.infoBox__title {
  text-align: center;
  text-transform: uppercase;
  color: black !important;
  font-weight: bold !important;
  padding-bottom: 15%;
}

.infoBox__cases, .infoBox__total {
  text-align: center;
  margin-bottom: 10%;
}

.app__graphTitle {
  margin-top: 10%;
  margin-bottom: 5%;
  text-transform: uppercase;
}

@media (max-width: 990px) { /*  SMARTPHONES */
  .app {
    flex-direction: column;
    padding: 10px;
  }

  .app__header, .app__stats {
    padding-left: 10px;
    padding-right: 10px;
  }

  .app__graphTitle {
    margin-left: 10px;
    margin-right: 10px;
  }

  .app__graph {
    margin: auto;
    padding-left: 10px;
    padding-right: 10px;
  }

  .app__right {
    margin-top: 8%;
  }

  h1 {
    font-size: 20px;
    text-transform: uppercase;
  }
  h3 {
    font-size: 15px;
    text-transform: uppercase;
  }
  
  .visit-me{
    display: none;
  }
}

@media (min-width: 1025px) { /* TABLETS AND PCs */
  .app__left {
    margin-right: 5%;
  }

  h1 {
    font-size: 28px;
    text-transform: uppercase;
  }
  h3 {
    font-size: 20px;
    text-transform: uppercase;
  }

  .visit-me {
    display: flex;
    justify-content: center !important;
    align-items: flex-end !important;
    position: absolute;
    left: 0;
    bottom: 5%;  
  }
  
  .visit-me-link {
    font-size: 15px;
    font-weight: 600;
    font-family: 'Spartan', sans-serif !important;
    background-color: black;
    color: white;
    text-decoration: none;
    padding: 1rem;
    text-transform: uppercase;
    transition: all .2s ease-in;
  }
  
  .visit-me-link:hover {
    padding-left: 30px;
  }
}
.infoBox {
  flex: 1 1;
}

.infoBox:not(:last-child) {
  margin-right: 15px;
}

@media (min-width: 1025px) {
  .infoBox__cases {
    color: #cc1034;
    font-weight: 600;
    font-size: 1.5rem;
    margin-bottom: 1.5rem;
  }
  .infoBox__cases_today {
    text-align: center;
    padding-bottom: 10px;
    text-transform: uppercase;
    font-size: 1rem;
    color: #161616 !important;
  }
}

@media (max-width: 990px) {
  .infoBox__cases {
    color: #cc1034;
    font-weight: 600;
    font-size: 1.1rem;
    margin-bottom: 1rem;
  }
  .infoBox__cases_today {
    text-align: center;
    padding-bottom: 10px;
    text-transform: uppercase;
    font-size: 0.8rem;
    color: #161616 !important;
  }
}

/*.infoBox--selected {
  border-top: 10px solid green;
}*/

/*.infoBox--red {
  border-color: red;
}*/

.infoBox__cases--green {
  color: green;
}

/*.infoBox--grey {
  border-color: #6A040F;
}*/

.infoBox__cases--grey {
  color: #6A040F;
}

.infoBox__title {
  font-weight: 700 !important;
  font-family: 'Spartan', sans-serif !important;
  text-decoration: underline;
}

@media (min-width: 1025px) {
  .infoBox__title {
    font-size: 1.3rem !important;
    text-decoration: underline;
  }
}

.infoBox__total {
  color: #161616 !important;
  font-weight: 400 !important;
  font-size: 0.8rem !important;
  margin-top: 15px !important;
  text-transform: uppercase;
  font-family: 'Spartan', sans-serif !important;
}

@media (min-width: 1025px) {
  .infoBox__total {
    font-size: 1rem !important;
  }
}

@media (max-width: 990px) {
  .infoBox__total {
    font-size: 0.8rem !important;
  }
}

.infoBox__total_plainText {
  text-transform: lowercase;
  font-weight: bold !important;
}

@media (max-width: 990px) {
  .infoBox__total_plainText {
    font-weight: bold !important;
    text-transform: lowercase;
    display: block;
  }
}
.table {
  margin-top: 20px;
  overflow: scroll;
  height: 400px;
  color: #6a5d5d;
  background-color: #fff;
}

.table tr {
  display: flex;
  justify-content: space-between;
}

.table td {
  padding: 0.5rem;
}
.table tr:nth-of-type(odd) {
  background-color: #f3f2f8;
}

